import React, { Component } from 'react';
import WwOne from './ww/one';
import WwTwo from './ww/two';
import WwThree from './ww/three';
import WwFour from './ww/four';



class WwComp extends Component {   


    state = {
        index: 0, 
        pageList: [<WwOne/>, <WwTwo/>, <WwThree/>, <WwFour/>]
      }
      
      onClickNext= () => {
          if (this.state.index + 1 === this.state.pageList.length ){
              this.setState({ 
                  index: 0 
                })
            } else {
                this.setState({
                    index: this.state.index + 1
                })
            }

          }
          onClickPrevious= () => {
            if (this.state.index - 1 === -1 ){
                this.setState({ 
                    index: this.state.pageList.length - 1
                  })
              } else {
                  this.setState({
                      index: this.state.index - 1
                  })
              }
            }
      
      render() {
        return (
          <div>
            {this.state.pageList[this.state.index]} 
            <div className="footerButtons">
            <button style={{"fontSize":"18px"}} onClick={this.onClickPrevious}> Previous </button>
            <button className="nextButton" style={{"margin-left":"5px", "fontSize":"18px"}} onClick={this.onClickNext}> Next </button>
            </div>
          </div>
        );
      }

}


export default WwComp;
