import React, { Component } from 'react';
import DsOne from './ds/one';
import DsTwo from './ds/two';
import DsThree from './ds/three';
import DsFour from './ds/four';
import DsFive from './ds/five';
import DsSix from './ds/six';
import DsSeven from './ds/seven';
import DsEight from './ds/eight';
import DsNine from './ds/nine';
import DsTen from './ds/ten';

class DsComp extends Component {   


    state = {
        index: 0, 
        pageList: [<DsOne/>, <DsTwo/>, <DsThree/>, <DsFour/>, <DsFive/>, <DsSix/>, <DsSeven/>, <DsEight/>, <DsNine/>, <DsTen/>]
      }
      
      onClickNext= () => {
          if (this.state.index + 1 === this.state.pageList.length ){
              this.setState({ 
                  index: 0 
                })
            } else {
                this.setState({
                    index: this.state.index + 1
                })
            }

          }
          onClickPrevious= () => {
            if (this.state.index - 1 === -1 ){
                this.setState({ 
                    index: this.state.pageList.length - 1
                  })
              } else {
                  this.setState({
                      index: this.state.index - 1
                  })
              }
            }
      
      render() {
        return (
          <div>
            {this.state.pageList[this.state.index]} 
            <div className="footerButtons">
            <button style={{"fontSize":"18px"}} onClick={this.onClickPrevious}> Previous </button>
            <button className="nextButton" style={{"margin-left":"5px", "fontSize":"18px"}} onClick={this.onClickNext}> Next </button>
            </div>
          </div>
        );
      }

}


export default DsComp;
