import React, { Component } from 'react';
import StdmOne from './stdm/one';
import StdmTwo from './stdm/two';
import StdmThree from './stdm/three';
import StdmFour from './stdm/four';
import StdmFive from './stdm/five';
import StdmSix from './stdm/six';
import StdmSeven from './stdm/seven';
import StdmEight from './stdm/eight';
import StdmNine from './stdm/nine';
import StdmTen from './stdm/ten';
import StdmEleven from './stdm/eleven';
import StdmTwelve from './stdm/twelve';
import StdmThirteen from './stdm/thirteen';
import StdmFourteen from './stdm/fourteen';



class StdmComp extends Component {   


    state = {
        index: 0, 
        pageList: [<StdmOne/>, <StdmTwo/>, <StdmThree/>, <StdmFour/>, <StdmFive/>, <StdmSix/>, <StdmSeven/>, <StdmEight/>, <StdmNine/>, <StdmTen/>, <StdmEleven/>, <StdmTwelve/>, <StdmThirteen/>, <StdmFourteen/>  ]
      }
      
      onClickNext= () => {
          if (this.state.index + 1 === this.state.pageList.length ){
              this.setState({ 
                  index: 0 
                })
            } else {
                this.setState({
                    index: this.state.index + 1
                })
            }

          }
          onClickPrevious= () => {
            if (this.state.index - 1 === -1 ){
                this.setState({ 
                    index: this.state.pageList.length - 1
                  })
              } else {
                  this.setState({
                      index: this.state.index - 1
                  })
              }
            }
      
      render() {
        return (
          <div>
            {this.state.pageList[this.state.index]} 
            <div className="footerButtons">
            <button style={{"fontSize":"18px"}} onClick={this.onClickPrevious}> Previous </button>
            <button className="nextButton" style={{"margin-left":"5px", "fontSize":"18px"}} onClick={this.onClickNext}> Next </button>
            </div>
          </div>
        );
      }

}


export default StdmComp;
