            <p>Their stay on the Great Orme was coming to an end, they could sense that. They would miss this place, truly it was beautiful. The younger monk had sat in fascination for hours watching the Kashmiri goats effortlessly scale the sheer cliff faces. He had even befriended one of them, or rather one of the goats had made a friend of him. It had become enamoured with the delightful flavour of the monk’s robe one night as he slept. When he awoke to find the animal chewing lazily on his finest (only) garment he had been initially rather upset with the goat. He had said some rather unpleasant things to the goat. He had thrown some rather weighty objects at the goat in an attempt to get the brazenly stupid animal to understand that what it was doing was wrong. All to no avail. The goat simply could not care what the monk wanted, it continued to chew lazily on the delicious robe. Being astonished by the unabashed thick brained stubborness of the animal the monk began to calm down. How foolish he had been, he thought, for allowing his emotions to control him so. Over something as simple as a robe, against something as simple as this goat. His master would not be pleased. He approached the goat and laid a hand on its head. It continued to chew. </p>
